<template>
  <v-app>
    <v-container grid-list-md text-center>
      <v-layout justify-center row>
        <v-layout align-center column fill-height wrap>
          <v-card class="v-card-profile text-center" style="border-radius: 2vh">
            <v-card-text class="text-xs-center">
              <h3 class="card-title font-weight-light">Team Management</h3>
              <v-divider></v-divider>
              <v-card class="v-card-profile text-center" style="width: 100vh">
                <v-card-text class="text-xs-center">
                  <h3 class="card-title font-weight-light">Current Members</h3>

                  <v-list v-for="(item, key, index) in listOfMembers" :key="index">
                    <v-list-group v-if="!(item.userKey == databaseUser.userKey)">
                      <template v-slot:activator>
                        <v-list-item-title>
                          {{item.email}} ({{item.role}})
                          <manager v-if="item.teamManager">*</manager>
                        </v-list-item-title>
                      </template>

                      <v-list-group
                        v-if="databaseUser.teamManager"
                        no-action
                        sub-group
                        value="true"
                      >
                        <template v-slot:activator>
                          <v-list-item-content>
                            <v-list-item-title>Manager Actions</v-list-item-title>
                          </v-list-item-content>
                        </template>

                        <v-list-item
                          v-if="!item.teamManager"
                          @click="setManagerLocal(item.userKey, true, index)"
                          link
                        >
                          <v-list-item-title>Promote as Team Manager</v-list-item-title>
                          <v-list-item-icon>
                            <v-icon>mdi-account-plus</v-icon>
                          </v-list-item-icon>
                        </v-list-item>
                        <v-list-item
                          v-if="item.teamManager"
                          @click="setManagerLocal(item.userKey, false, index)"
                          link
                        >
                          <v-list-item-title>Remove as Team Manager</v-list-item-title>
                          <v-list-item-icon>
                            <v-icon>mdi-account-minus</v-icon>
                          </v-list-item-icon>
                        </v-list-item>
                        <v-list-item @click="removeMemberLocal(item.userKey, item.teamKey)" link>
                          <v-list-item-title>Remove From Team</v-list-item-title>
                          <v-list-item-icon>
                            <v-icon>mdi-account-remove</v-icon>
                          </v-list-item-icon>
                        </v-list-item>
                      </v-list-group>

                      <v-list-group sub-group no-action>
                        <template v-slot:activator>
                          <v-list-item-content>
                            <v-list-item-title>Assign Role</v-list-item-title>
                          </v-list-item-content>
                        </template>
                        <v-list-item v-if="!(item.role == 'Observer')" @click="setRoleLocal(item.userKey, 'Observer')" link>
                          <v-list-item-title>Set as Observer</v-list-item-title>
                          <v-list-item-icon>
                            <v-icon>mdi-eye</v-icon>
                          </v-list-item-icon>
                        </v-list-item>
                        <v-list-item v-if="!(item.role == 'Pitcrew')" @click="setRoleLocal(item.userKey, 'Pitcrew')" link>
                          <v-list-item-title>Set as Pitcrew</v-list-item-title>
                          <v-list-item-icon>
                            <v-icon>mdi-cards</v-icon>
                          </v-list-item-icon>
                        </v-list-item>
                        <v-list-item v-if="!(item.role == 'Driver')" @click="setRoleLocal(item.userKey, 'Driver')" link>
                          <v-list-item-title>Set as Driver</v-list-item-title>
                          <v-list-item-icon>
                            <v-icon>mdi-car</v-icon>
                          </v-list-item-icon>
                        </v-list-item>
                      </v-list-group>
                    </v-list-group>
                  </v-list>

                  <v-divider></v-divider>
                  <h3 class="card-title font-weight-light">Send An Invite via Email</h3>
                  <div class="input" :class="{invalid: $v.email.$error}">
                    <p
                      v-if="!$v.email.email  && $v.email.required && $v.email.$dirty"
                    >Please provide a valid email address.</p>
                  </div>
                  <v-text-field
                    v-model="email"
                    :outlined="true"
                    :rounded="true"
                    :clearable="true"
                    @blur="$v.email.$touch()"
                  ></v-text-field>
                  <v-btn
                    color="success"
                    rounded
                    class="font-weight-light"
                    x-large
                    style="border-radius:2vh"
                    @click="sendInvite"
                    :disabled="$v.$invalid || sending"
                  >Send Invitation</v-btn>
                </v-card-text>
              </v-card>
            </v-card-text>
          </v-card>
        </v-layout>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      email: "",
      sending: false,
      randomPassword: null
    };
  },
  computed: {
    databaseUser() {
      return this.$store.getters.currentDatabaseUser;
    },
    listOfMembers() {
      return this.$store.getters.currentMembersDetails.membersDetails;
    }
  },
  validations: {
    email: {
      required,
      email
    }
  },
  mounted() {
    this.$nextTick(function() {
      let emailJSscript = document.createElement("script");
      emailJSscript.setAttribute(
        "src",
        "https://cdn.jsdelivr.net/npm/emailjs-com@2.3.2/dist/email.min.js"
      );
      document.head.appendChild(emailJSscript);
    });
  },
  methods: {
    ...mapActions([
      "addMemberInDatabase",
      "createMemberInAuth",
      "getMembersFromDatabase",
      "setManager",
      "setRole",
      "removeMember"
    ]),
    setManagerLocal(userKey, value) {
      var payload = {
        userKey,
        value
      };
      this.setManager(payload);
    },
    setRoleLocal(userKey, value) {
      var payload = {
        userKey,
        value
      };
      this.setRole(payload);
    },
    removeMemberLocal(userKey, teamKey) {
      var payload = {
        userKey,
        teamKey
      }
      this.removeMember(payload);
    },
    sendInvite() {
      this.sending = true;
      this.sendRequestToAddMember();
    },
    sendRequestToAddMember() {
      this.randomPassword = String(
        Math.floor(10000000 + Math.random() * 900000)
      );
      var payload1 = {
        email: this.email,
        password: this.randomPassword
      };
      this.createMemberInAuth(payload1).then(() => {
        var payload2 = {
          teamKey: this.$store.getters.currentDatabaseUser.teamKey,
          email: this.email
        };
        this.addMemberInDatabase(payload2).then(() => {
          this.sendEmail(this.email);
        });
      });
    },
    sendEmail(email) {
      event.preventDefault();
      // Send invitation email using EmailJS
      emailjs.init("user_NjsqTvqcSj3KgQY5BlS8S");
      var link = this.email + "-;-" + this.randomPassword;
      emailjs
        .send("gmail", "semicircle_invitation", {
          to_email: this.email,
          link_html:
            '<a href="http://localhost:8080/#/Invited/' +
            link +
            '">Set up your account</a>'
        })
        .then(
          response => {
            this.sending = false;
          },
          error => {
            this.sending = false;
          }
        );
    }
  }
};
</script>

<style scoped>
.input.invalid p {
  color: red;
}
</style>


