<template>
  <v-app>
    <v-container fluid v-if="!registerMode">
      <v-form>
        <v-layout justify-center row>
          <v-layout align-center column fill-height>
            <v-flex xs12>
              <v-card class="v-card-profile text-center" style="width: 50vh; border-radius: 2vh">
                <v-avatar class="mx-auto d-block" size="100">
                  <img src="../../assets/logo.png" />
                </v-avatar>
                <v-card-text class="text-xs-center">
                  <h3 class="card-title font-weight-light">Enter Your TeamSpace</h3>
                  <v-alert type="error" v-if="errorMessage != ''">{{errorMessage}}</v-alert>
                  <h4>
                    Email
                    <div class="input" :class="{invalid: $v.email.$error}">
                      <p
                        v-if="!$v.email.email && $v.email.required && $v.email.$dirty"
                      >Please provide a valid email address.</p>
                      <p
                        v-if="!$v.email.required && $v.email.email && $v.email.$dirty"
                      >This field must not be empty.</p>
                      <v-text-field
                        v-model="email"
                        :outlined="true"
                        :rounded="true"
                        :clearable="true"
                        @blur="$v.email.$touch()"
                      ></v-text-field>
                    </div>Password
                    <div class="input" :class="{invalid: $v.password.$error}">
                      <p
                        v-if="!$v.password.required && $v.password.$dirty"
                      >Please provide a password.</p>
                      <p
                        v-if="!$v.password.minLen && $v.password.$dirty"
                      >Please provide a password at least 6 characters long.</p>
                      <v-text-field
                        v-model="password"
                        :outlined="true"
                        :rounded="true"
                        :clearable="true"
                        type="password"
                        @blur="$v.password.$touch()"
                      ></v-text-field>
                    </div>
                  </h4>
                  <v-btn
                    color="success"
                    rounded
                    class="font-weight-light"
                    x-large
                    style="border-radius:2vh"
                    @click="loginLocal"
                    :disabled="$v.$invalid"
                  >Log In</v-btn>
                  <h5>
                    <a @click="registerMode = true">Register a new Team</a>
                  </h5>
                  <h5>
                    <a>Forgot your password?</a>
                  </h5>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-layout>
      </v-form>
    </v-container>

    <app-Register
      v-if="registerMode"
      :registerMode="registerMode"
      @cancelRegistration="registerMode = $event"
    ></app-Register>
  </v-app>
</template>

<script>
import { required, email, minLength } from "vuelidate/lib/validators";
import { mapActions} from "vuex";
import Register from "./Register.vue";

export default {
  data() {
    return {
      registerMode: false,
      email: "",
      password: "",
      errorMessage: ""
    };
  },
  validations: {
    email: {
      required,
      email
    },
    password: {
      required,
      minLen: minLength(6)
    }
  },
  components: {
    appRegister: Register
  },
  created() {
    var token = localStorage.getItem("token");
    var fbUser = localStorage.getItem("firebaseUser");

    if (token && fbUser) {
      this.setFirebaseUser();
      this.getUserDetailsFromDatabase();
      this.tryAutoLogin(token).then(() => {
        this.$router.push({ name: "Dashboard" });
      });
    }
  },
  methods: {
    ...mapActions(["login", "tryAutoLogin", "setFirebaseUser", "getUserDetailsFromDatabase"]),

    loginLocal() {
      var payload = {
        email: this.email,
        password: this.password
      };
      this.login(payload)
        .then(() => {
          this.setFirebaseUser();
          this.getUserDetailsFromDatabase();
          this.email = "";
          this.password = "";
          this.$router.push({ name: "Dashboard" });
        })
        .catch(error => {
          this.errorMessage = error.message;
          this.password = "";
        });
    }
  }
};
</script>

<style scoped>
.input.invalid p {
  color: red;
}
</style>


