<template>
  <v-app>
    <div v-if="databaseUser.hasTeam">
      TeamSpace Placeholder
      <div>firebaseUser: {{ firebaseUser }}</div>
      <div>databaseUser: {{ databaseUser }}</div>
      <br />
      <ul>
        <li v-for="(item, key, index) in listOfMembers" :key="index">{{item}}</li>
      </ul>
    </div>

    <div v-if="!databaseUser.hasTeam && databaseUser">
      <v-container grid-list-md text-center>
        <v-layout justify-center row>
          <v-layout align-center column fill-height wrap>
            <v-card class="v-card-profile text-center" style="border-radius: 2vh">
              <v-card-text class="text-xs-center">
                <h3 class="card-title font-weight-light">Sorry to Interrupt but..</h3>
                <v-divider></v-divider>
                <v-card class="v-card-profile text-center" style="width: 100vh">
                  <v-card-text class="text-xs-center">
                    <h3 class="card-title font-weight-light">You currently do not have a team.</h3>

                    <v-divider></v-divider>
                    <h4
                      class="card-title font-weight-light"
                    >Please click the button below to reset your account so you may register a new team or accept invites from another team.</h4>

                    <v-btn
                      color="success"
                      rounded
                      class="font-weight-light"
                      x-large
                      style="border-radius:2vh"
                      @click="deleteUserLocal"
                    >Reset Account</v-btn>
                  </v-card-text>
                </v-card>
              </v-card-text>
            </v-card>
          </v-layout>
        </v-layout>
      </v-container>
    </div>
  </v-app>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      //
    };
  },
  computed: {
    firebaseUser() {
      return this.$store.getters.currentFirebaseUser;
    },
    databaseUser() {
      return this.$store.getters.currentDatabaseUser;
    },
    listOfMembers() {
      return this.$store.getters.currentMembersDetails.membersDetails;
    }
  },
  methods: {
    ...mapActions(["deleteUser"]),
    deleteUserLocal() {
      var payload = {
        userKey: this.databaseUser.userKey
      };
      this.deleteUser(payload);
      this.$router.push({ name: "Login" });
    }
  }
};
</script>


