<template>
  <v-toolbar id="core-toolbar" absolute tile style="background: #ffc300;">
    <div class="v-toolbar-title">
      <v-toolbar-items v-if="databaseUser.hasTeam">
        <v-flex align-center layout py-2>
          <v-btn icon>
            <v-icon color="tertiary" @click="changeView('TeamSpace')">mdi-view-dashboard</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon color="tertiary" @click="changeView('StartRun')">mdi-road-variant</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon color="tertiary" @click="changeView('WatchRun')">mdi-file-word-box</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon color="tertiary" @click="changeView('DriverSetup')">mdi-car</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon color="tertiary" @click="changeView('CrewpitSetup')">mdi-cards</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon color="tertiary" @click="changeView('TeamManagement')">mdi-account-multiple</v-icon>
          </v-btn>
          <v-btn @click="logoutLocal()">Logout</v-btn>
        </v-flex>
      </v-toolbar-items>
    </div>
  </v-toolbar>
</template>

<script>
import { mapActions } from "vuex";

export default {
  props: ["currentView"],
  data() {
    return {
      //
    };
  },
  computed: { 
    databaseUser () {
      return this.$store.getters.currentDatabaseUser;
    }
  },
  methods: {
    ...mapActions(["logout"]),
    changeView(value) {
      this.currentView = value;
      this.$emit("changeView", this.currentView);
    },
    logoutLocal() {
      this.$router.push({ name: "Login" });
      this.logout();
    }
  }
};
</script>



