<template>
  <v-app>
    <v-navigation-drawer
      id="app-drawer"
      v-model="inputValue"
      app
      dark
      floating
      persistent
      mobile-break-point="991"
      width="260"
    >
      <v-img :src="logo" :gradient="sidebarOverlayGradiant" height="100%">
        <v-layout class="fill-height" tag="v-list" column>
          <v-avatar class="mx-auto d-block" color="white" size="100">
            <img src="../../assets/logo.png" />
          </v-avatar>
          <v-list rounded>
            <v-list-item-group v-model="item" color="primary">
              <v-list-item v-for="(link, i) in links" :key="i" @click="setView(link.to)">
                <v-list-item-icon>
                  <v-icon v-text="link.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="link.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-layout>
      </v-img>
    </v-navigation-drawer>
    <app-TeamSpace v-if="currentView == '/TeamSpace'"></app-TeamSpace>
    <app-StartRun v-if="currentView == '/StartRun'"></app-StartRun>
    <app-WatchRun v-if="currentView == '/WatchRun'"></app-WatchRun>
  </v-app>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import TeamSpace from "../TeamSpace/TeamSpace.vue";
import StartRun from "../Run/StartRun.vue";
import WatchRun from "../Run/WatchRun.vue"

export default {
  data() {
    return {
      logo: "../../assets/logo.png",
      currentView: "/TeamSpace",
      links: [
        {
          to: "/TeamSpace",
          icon: "mdi-view-dashboard",
          text: "TeamSpace",
          for: "Public"
        },
        {
          to: "/StartRun",
          icon: "mdi-flag-checkered",
          text: "Start a Run",
          for: "DriverAndCrewpit"
        },
        {
          to: "/WatchRun",
          icon: "mdi-camcorder",
          text: "Watch a Run",
          for: "Public"
        },
        {
          to: "/DriverSetup",
          icon: "mdi-bike",
          text: "Driver's Setup",
          for: "Driver"
        },
        {
          to: "/CrewpitSetup",
          icon: "mdi-walk",
          text: "Crewpit Setup",
          for: "Crewpit"
        },
        {
          to: "/TeamManagement",
          icon: "mdi-account-multiple",
          text: "Team",
          for: "Public"
        },
        {
          to: "/test",
          icon: "mdi-logout",
          text: "Log Out",
          for: "Public"
        }
      ],
      responsive: false
    };
  },
  components: {
    appTeamSpace: TeamSpace,
    appStartRun: StartRun,
    appWatchRun: WatchRun
  },
  computed: {
    // Change conditions to current user's role status
    FilteredLinks: function() {
      return this.links.filter(function(item) {
        if (true) {
          if (item.for == 'Public' || item.for == 'Driver' || item.for == 'DriverAndCrewpit' && item.for != 'Crewpit')
            return item;
        } else if (true) {
          if (item.for == 'Public' || item.for == 'Crewpit' || item.for == 'DriverAndCrewpit' && item.for != 'Driver')
            return item;
        } else {
          if (item.for == 'Public')
            return item;
        }
      });
    }},
    //     ...mapState('app', ['image', 'color']),
    //     inputValue: {
    //       get () {
    //         return this.$store.state.app.drawer
    //       },
    //       set (val) {
    //         this.setDrawer(val)
    //       }
    //     },
        //  items () {
        //    return this.$t('Layout.View.items')
        //  },
    //     sidebarOverlayGradiant () {
    //       return `${this.$store.state.app.sidebarBackgroundColor}, ${this.$store.state.app.sidebarBackgroundColor}`
    //     }
    //   },
    //   mounted () {
    //     this.onResponsiveInverted()
    //     window.addEventListener('resize', this.onResponsiveInverted)
    //   },
    //   beforeDestroy () {
    //     window.removeEventListener('resize', this.onResponsiveInverted)
    //   },
       methods: {
    //     ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    //     onResponsiveInverted () {
    //       if (window.innerWidth < 991) {
    //         this.responsive = true
    //       } else {
    //         this.responsive = false
    //       }
        setView(value) {
        return (this.currentView = value);
      }
  }
};
</script>

<style lang="scss">
#app-drawer {
  .v-list__tile {
    border-radius: 4px;

    &--buy {
      margin-top: auto;
      margin-bottom: 17px;
    }
  }

  .v-image__image--contain {
    top: 9px;
    height: 60%;
  }

  .search-input {
    margin-bottom: 30px !important;
    padding-left: 15px;
    padding-right: 15px;
  }

  div.v-responsive.v-image > div.v-responsive__content {
    overflow-y: auto;
  }
}
</style>



