<template>
  <v-app>
    <div>Device Status: {{ status }}</div>
    <hr/>
    <div>Connection: {{connection }}</div>
    <hr/>
    <div>Video: {{ video }} </div>
    <v-btn @click="openOrJoinRoom">Open or Join Room</v-btn>
    <div id="selfVideo"></div>
    <div id="remoteVideo"></div>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      connection: new RTCMultiConnection(),
      status: false,
      video: false
    };
  },
  created() {
    // var app = {
    //   initialize: () => {
    //     app.bindEvents();
    //   },
    //   bindEvents: () => {
    //     document.addEventListener("deviceready", app.onDeviceReady, false);
    //   },
    //   onDeviceReady: () => {
    //     this.status = true;
    //     // var connection = new RTCMultiConnection();
    //     this.connection.socketUrl = "https://rtcmulticonnection.herokuapp.com:443/";
    //     this.connection.session = {
    //       audio: true,
    //       video: true
    //     };
    //     this.connection.sdpConstraints.mandatory = {
    //       OfferToReceiveAudio: true,
    //       OfferToReceiveVideo: true
    //     };
    //     var selfVideoContainer = document.getElementById("selfVideo");
    //     var remoteVideoContainer = document.getElementById("remoteVideo");

    //     this.connection.onstream = event => {
    //       var video = event.mediaElement;

    //       if (event.type === "local") {
    //         selfVideoContainer.appendChild(video);
    //       }

    //       if (event.type === "remote") {
    //         remoteVideoContainer.appendChild(video);
    //       }
    //     };
    //     this.connection.openOrJoin("semicircleroom");
    //   }
    // };

    // app.initialize();
  },
  computed: {
    databaseUser() {
      return this.$store.getters.currentDatabaseUser;
    }
  },
  methods: {
    //...mapActions(["login", "tryAutoLogin", "setFirebaseUser", "getUserDetailsFromDatabase"]),

    openOrJoinRoom() {
      this.connection.socketURL =
        "https://rtcmulticonnection.herokuapp.com:443/";

      this.connection.session = {
        audio: true,
        video: true
      };

      this.connection.sdpConstraints.mandatory = {
        OfferToReceiveAudio: true,
        OfferToReceiveVideo: true
      };

      var selfVideoContainer = document.getElementById("selfVideo");
      var remoteVideoContainer = document.getElementById("remoteVideo");

      // isVideoSupportsStreamCapturing must be true?

      this.connection.onstream = event => {
        var video = event.mediaElement;
        
        var constraints = {
          width: 400,
          height: 400
        }
        this.connection.applyConstraints({
          video: constraints
        })

        this.video = video;

        if (event.type === "local") {
          selfVideoContainer.appendChild(video);
        }

        if (event.type === "remote") {
          remoteVideoContainer.appendChild(video);
        }
      };

      this.connection.openOrJoin("semicircleroom");
    }
  }
};
</script>

<style scoped>

/* #selfVideo{
  height: 300px;
  width: 300px
} */

</style>


