import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Welcome/Login'
import Dashboard from '@/components/shared/Dashboard'
import Test from '@/components/shared/test'
import TeamSpace from '@/components/TeamSpace/TeamSpace'
import TeamManagement from '@/components/TeamManagement/TeamManagement'
import StartRun from '@/components/Run/StartRun'
import WatchRun from '@/components/Run/WatchRun'
import DriverSetup from '@/components/Setup/DriverSetup'
import CrewpitSetup from '@/components/Setup/CrewpitSetup'
import Invited from '@/components/TeamManagement/Invited'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
    path: '/test',
    name: 'test',
    component: Test
    },
    {
      path: '/TeamSpace',
      name: 'TeamSpace',
      component: TeamSpace
    },
    {
      path: '/StartRun',
      name: 'StartRun',
      component: StartRun
    },
    {
      path: '/WatchRun',
      name: 'WatchRun',
      component: WatchRun
    },
    {
      path: '/TeamManagement',
      name: 'TeamManagement',
      component: TeamManagement
    },
    {
      path: '/DriverSetup',
      name: 'DriverSetup',
      component: DriverSetup
    },
    {
      path: '/CrewpitSetup',
      name: 'CrewpitSetup',
      component: CrewpitSetup
    },
    {
      path: '/Invited/:link',
      name: 'Invited',
      component: Invited
    },
    {path: '*', redirect: '/' }
  ]
})



